.contacts {
    display: flex;
    align-items: center;
    padding-bottom: 10px;
}

.contacts__address {
    @include clear-link;
    flex-grow: 1;
    padding-left: 25px;
    background: url(../../img/pin.svg) no-repeat 0 center;
    background-size: 3%;

    &:hover {
        color: $color-nobel;
    }
}

.contacts__tel {
    @include clear-link;
    margin-right: 30px;
    display: block;

    &:hover {
        color: $color-nobel;
    }
}

.contacts__write-us {
    padding: 5px 10px;
    border-radius: 10px;
    border: none;
    background-color: $color-opal;
    font-weight: 600;

    &:hover {
        color: white;
        transform: scale(110%);
    }
}

@media (max-width: $tablet-width) {
    .contacts {
        display: none;
    }

    .footer { 
        & .contacts {
        display: block;

        flex-direction: column;
        justify-content: center;
        align-items: center;
        }

        & .contacts__address,
        & .contacts__tel {
            padding-top: 10px;
            text-align: center;
        }

        & .contacts__write-us {
            display: block;
            margin: 0 auto;
            margin-top: 20px;
        }
    }
}