.contact .popup__wrapper {
    background-color: $color-opal;
    padding: 0;
}

.contact__header {
    text-align: center;
    height: 100px;
    margin: 0;
    margin-bottom: 20px;

    color: $color-san-juan;
    font-size: 40px;
    font-weight: 600;
    line-height: 100px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;

    background: url(../../img/logo-wall.jpg) no-repeat center center;
    background-size: 100%;
}

.contact__list {
    @include clear-list;
    display: flex;
    justify-content: space-around;
    margin-bottom: 10px;
    padding-bottom: 20px;

    border-bottom: 1px solid $color-san-juan;
}

.contact__item {
    padding: 20px;
    border: 1px solid $color-san-juan;
    border-radius: 15px;
}

.contact__tel,
.contact__address {
    @include clear-link;
}

.contakt__inner-wrap {
    display: flex;
}

.contakt__adress {
    padding-left: 20px;
}