.about {
    padding-top: 120px;
}

.about__header {
    text-align: center;
}

.about__wrapper {
    display: flex;
}

.about__slider {
    text-align: center;
    margin-right: 20px;
}

.about__img {
    width: 100%;
}

.about__text {
    width: 160%;
    margin-top: 0;
}

@media (max-width: $tablet-width) {
    .about__wrapper {
        display: block;
    }

    .about__img {
        width: 100%;
    }
    
    .about__text {
        width: 90%;
        margin-top: 10px;
    }
}